import React, { useState } from "react";
import { Button, Input, notification } from "antd";
import { TUserState } from "../../types/auth";
import { isAnyFieldEmpty } from "../../services/helpers";
import { dispatch } from "../../store";
import { register } from "../../store/auth.slice";
import "../../styles/Register.scss";


export const Register = ({onChange} : {onChange: (key: string) => void}) => {
    const [registerData, setRegisterData] = useState<TUserState>({    
        firstName: "",
        middleName: "",
        lastName: "",
        email: "",
        userName: "",
        password: "",
        verifyPassword: ""
    });

    const changeHandler = (val: string, key: string) => {
        setRegisterData(prevState => ({...prevState, [key]: val}));
    };

    const submitHandler = (e: React.FormEvent<HTMLFormElement>) => {
        e.preventDefault();
        if(!isAnyFieldEmpty(registerData)) {
            if(registerData.password === registerData.verifyPassword) {
                dispatch(register(registerData));
            } else {
                notification.warning({
                    message: "  .",
                    placement: "bottomRight"
                });
            };
        } else {
            notification.warning({
                message: "   .",
                placement: "bottomRight"
            });
        };
    };

    return (
        <div className="Register">
            <div className="title">
                <h2></h2>
            </div>

            <form onSubmit={submitHandler}>
                <Input placeholder="" onChange={(e) => changeHandler(e.target.value, "firstName")}/>
                <Input placeholder="" onChange={(e) => changeHandler(e.target.value, "middleName")}/>
                <Input placeholder="" onChange={(e) => changeHandler(e.target.value, "lastName")}/>
                <Input placeholder="" onChange={(e) => changeHandler(e.target.value, "email")}/>
                <Input placeholder="" onChange={(e) => changeHandler(e.target.value, "userName")}/>
                <Input.Password placeholder="" onChange={(e) => changeHandler(e.target.value, "password")}/>
                <Input.Password placeholder=" " onChange={(e) => changeHandler(e.target.value, "verifyPassword")}/>
                <Button htmlType="submit"></Button>

                <p onClick={() => onChange("auth")} className="login_account">  ? <span></span></p>
            </form>
        </div>
    );
};