import React, { useState } from "react";
import { pathGoogleIcon } from "../../constants";
import { Button, Input, notification } from "antd";
import { dispatch } from "../../store";
import { login } from "../../store/auth.slice";
import "../../styles/Auth.scss";

export const Auth = ({onChange} : {onChange: (key: string) => void}) => {
    const [loginData, setLoginData] = useState<{userName: string, password: string}>({userName: "", password: ""});
    
    const changeHandler = (val: string, key: string) => {
        setLoginData(prevState => ({...prevState, [key]: val}));
    };

    const submitHandler = (e: React.FormEvent<HTMLFormElement>) => {
        e.preventDefault();

        if(loginData.password && loginData.userName) {
            dispatch(login(loginData));
        } else {
            notification.warning({
                message: "   .",
                placement: "bottomRight"
            });
        };
    };

    return (
        <div className="Auth">
            <div className="title">
                <h2> </h2>
                <p>,   </p>
            </div>

            <div className="google_auth">
                <button>
                    <img src={pathGoogleIcon} alt="google_icon" />
                    Login with Google
                </button>
            </div>

            <div className="hr_border">
                <span>or Login with Email</span>
            </div>
            
            <form onSubmit={submitHandler}>
                <label></label>
                <Input placeholder="  " onChange={(e) => changeHandler(e.target.value, "userName")}/>

                <label></label>
                <Input.Password placeholder="  " onChange={(e) => changeHandler(e.target.value, "password")}/>

                <Button htmlType="submit"></Button>

                <p onClick={() => onChange("register")} className="create_account"> ? <span>  </span></p>
            </form>
        </div>
    );
};